.password-generator-page .manager .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-image: url('../../../../assets/PasswordGenerator/row-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:3.5% 10% 3.5% 5%;
    margin-bottom: 5%;
    .img{
        content: url('../../../../assets/PasswordGenerator/img1.png');
        width: 42.5%;
        &:hover{
            transform: scale(1.035);
            transition: 0.35s ease-in-out;
        }
    }
    .info{
        color:#fff;
        width: 37.5%;
    }
    h1{
        font-size: 40px;
    }
    p{
        font-size: 18px;
        margin:20px 0 45px;
    }
    a{
        padding:13.5px 35px;
        font-size: 18px;
        border-radius: 25px;
        border:1px solid #fff;
        color:#fff;
    }
}

.password-generator-page .manager .row2{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 10%;
    margin: 5% 0;
    .images{
        width: 40%;
        position: relative;
        &:hover{
            .circle{
                border-radius: 25px;
            }
            .img{
                transform: scale(1.035);
            }
        }
        
    }
    .info{
        width: 40%;
    }
    h1{
        font-size: 40px;
    }
    .p{
        font-size: 18px;
        margin:20px 0 40px;
    }
    .buttons{
        display: flex;
    }
    button{
        background:#111;
        color:#fff;
        border: 0;
        border-radius: 5px;
        padding:10px 15px;
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: 0.3s ease-in-out;
        &:first-child{
            margin-right: 20px;
        }
        p{
            font-size: 14px;
        }
        h6{
            font-size: 16px;
        }
        &:hover{
            transform: translateY(-7px);
        }
    }
    .icon{
        font-size: 30px;
        margin-right: 10px;
    }
    .img{
        content: url('../../../../assets/PasswordGenerator/img2.png');
        width: 70%;
        margin:0 auto;
        position: relative;
        z-index: 2;
        transition: 0.35s ease-in-out;
    }
    .circle{
        width: 100%;
        padding-top: 100%;
        border-radius: 50%;
        background:#f3f3f3;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        transition: 0.35s ease-in-out;
    }
}

@media(max-width:1401px){
    .password-generator-page .manager .row1{
        padding:3.5% 10% 3.5% 5%;
        margin: 5% 0;
        .info{
            width: 40%;
        }
        h1{
            font-size: 32.5px;
        }
        p{
            font-size: 15px;
            margin:20px 0 45px;
        }
        a{
            padding:12.5px 30px;
            font-size: 15px;
        }
    }
    
    .password-generator-page .manager .row2{
        padding:0 10%;
        h1{
            font-size: 32.5px;
        }
        .p{
            font-size: 15px;
            margin:20px 0 35px;
        }
        button{
            padding:10px 15px;
            p{
                font-size: 12px;
            }
            h6{
                font-size: 14px;
            }
        }
        .icon{
            font-size: 25px;
            margin-right: 7px;
        }
    }
}

@media(max-width:1005px){
    .password-generator-page .manager .row2 .info{
        width: 45%;
    }
}

@media(max-width:895px){

    .password-generator-page .manager .row1{
        padding:45px 7.5%;
        margin: 35px 0;
        flex-wrap: wrap;
        .info{
            width: 420px;
            max-width: 100%;
            margin:15px auto;
        }
        .img{
            width: 420px;
            max-width: 100%;
            margin:15px auto;
        }
        h1{
            font-size: 22.5px;
        }
        p{
            font-size: 15px;
            margin:15px 0 30px;
        }
        a{
            padding:10px 30px;
            font-size: 15px;
        }
    }
    
    .password-generator-page .manager .row2{
        padding:0 7.5%;
        margin:35px 0;
        flex-wrap: wrap;
        .info{
            width: 420px;
            max-width: 100%;
            margin:15px auto 25px;
        }
        .images{
            width: 420px;
            max-width: 100%;
            margin:25px auto 10px;
        }
        h1{
            font-size: 22.5px;
        }
        .p{
            margin:15px 0 25px;
        }
        .buttons{
            justify-content: center;
            flex-wrap: wrap;
        }
        button{
            padding:7.5px 10px;
            margin:7.5px;
            p{
                font-size: 11px;
            }
            h6{
                font-size: 13px;
            }
            &:first-child{
                margin:7.5px;
            }
        }
        .icon{
            font-size: 22px;
            margin-right: 8px;
        }
    }
}

.password-generator-page .manager .row1{
    a{        
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        background: #427CFF;
        border-radius: 25px;
        -webkit-transform: scale(0);
        transform: scale(0);
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    a:hover, a:focus, a:active {
        color: white;
        border:1px solid transparent;
        transition: 0.5s ease-in-out;
    }
    a:hover:before, a:focus:before, a:active:before {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}