.password-generator-page .pass-container{
    width:70%;
    margin:-5% auto 5%;
    border-radius: 15px;
    border:1px solid #f6f6f6;
    background-color: #fff;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    .inline1{
        padding: 40px 55px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 12.5px solid #9FFC9C;
        input{
            font-size: 35px;
            padding:10px;
            border:1px solid #9FFC9C;
            margin-right: 25px;
            border-radius: 5px;
        }
        .btns{
            display: flex;
            align-items: center;
        }
        .refresh{
            font-size: 30px;
            cursor: pointer;
            background: none;
            border: 0;
            margin: 0 20px -5px 0;
        }
        .copy{
            background: #427CFF;
            color:#fff;
            padding:12.5px 25px;
            border-radius: 25px;
            font-size: 18px;
            border:0;
            cursor: pointer;
        }
    }
    .second{
        padding:40px 50px;
    }
    h6{
        font-size: 25px;
    }
    .inline2{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin:50px 0;
        h5{
            width: fit-content;
            font-size: 22.5px;
        }
        .span{
            width: fit-content;
            margin: 0 50px;
            padding:2px 25px 5px;
            border-radius: 5px;
            font-size: 27.5px;
            border:1px solid #222;
        }
    }
    .rangeslider-horizontal{
        height: 12.5px;
        flex: 1;
    }
    .rangeslider__fill{
        background-color: #427CFF;
    }
    .rangeslider__handle{
        width:35px;
        height: 35px;
        background-color: #427CFF;
        box-shadow: none;
        border: 1px solid #427CFF;
    }
    .rangeslider__handle::after{
        opacity: 0;
    }
    .radios{
        display: flex;
        margin-bottom: 40px;
        .radio{
            display: flex;
            align-items: center;
            font-size: 19px;
            margin-right: 50px;
        }
        input{
            transform: scale(1.75);
            margin-right: 15px;
        }
    }
    .checks{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .check{
            display: flex;
            align-items: center;
            font-size: 19px;
            width: fit-content;
            margin:10px 20px 10px 0;
            &:last-child{
                margin:0;
            }
        }
        input{
            transform: scale(1.5);
            margin-right: 15px;
        }
    }
}

@media(max-width:1401px){
    .password-generator-page .pass-container{
        width:75%;
        margin:-5% auto 5%;
        box-shadow: rgba(50, 50, 93, 0.1) 0px 2px 3px -1px, rgba(0, 0, 0, 0.2) 0px 1px 3px -1px;
        .inline1{
            padding: 25px 35px;
            border-bottom: 10px solid #9FFC9C;
            input{
                font-size: 27.5px;
                margin-right: 20px;
            }
            .refresh{
                font-size: 25px;
                margin: 0 20px -5px 0;
            }
            .copy{
                padding:12px 25px;
                border-radius: 25px;
                font-size: 15px;
            }
        }
        .second{
            padding:25px 35px;
        }
        h6{
            font-size: 20px;
        }
        .inline2{
            margin:35px 0;
            h5{
                font-size: 18.5px;
            }
            .span{
                margin: 0 40px;
                padding:1px 20px 4px;
                font-size: 22.5px;
            }
        }
        .rangeslider-horizontal{
            height: 10px;
        }
        .rangeslider__handle{
            width:30px;
            height: 30px;
        }
        .radios{
            margin-bottom: 30px;
            .radio{
                font-size: 16px;
                margin-right: 40px;
            }
            input{
                transform: scale(1.5);
                margin-right: 10px;
            }
        }
        .checks{
            .check{
                font-size: 16px;
                margin:10px 20px 10px 0;
                &:last-child{
                    margin:0;
                }
            }
            input{
                transform: scale(1.3);
                margin-right: 10px;
            }
        }
    }
}

@media(max-width:991px){
    .password-generator-page .pass-container{
        width:90%;
    }
}

@media(max-width:767px){
    .password-generator-page .pass-container{
        width:85%;
        margin:-55px auto 35px;
        border-radius: 10px;
        box-shadow: rgba(50, 50, 93, 0.1) 0px 1px 2.5px -0.5px, rgba(0, 0, 0, 0.1) 0px 0.5px 1.5px -0.5px;
        .inline1{
            padding: 20px;
            display: block;
            border-bottom: 7.5px solid #9FFC9C;
            input{
                width: 100%;
                font-size: 25px;
                padding:10px;
                margin:10px 0;
            }
            .btns{
                width: 100%;
                justify-content: center;
                margin:10px 0;
            }
            .refresh{
                font-size: 24px;
                margin: 0 20px -5px 0;
            }
            .copy{
                padding:12.5px 25px 10px;
                border-radius: 25px;
            }
        }
        .second{
            padding:20px;
        }
        h6{
            font-size: 20px;
            text-align: center;
        }
        .inline2{
            display: block;
            margin:20px 0;
            text-align: center;
            h5{
                width: 100%;
                text-align: center;
                font-size: 17.5px;
                margin-bottom: 20px;
            }
        }
        .rangeslider-horizontal{
           margin-top: 30px;
        }
   
        .radios{
            flex-wrap: wrap;
            justify-content: center;
            margin: 30px 0 15px;
            .radio{
               margin:10px 20px;
            }
        }
        .checks{
            justify-content: center;
            padding-top: 20px;
            border-top: 1px solid #9FFC9C;
            .check{
               margin:10px 20px;
                &:last-child{
                    margin:10px 20px;
                }
            }
        }
    }
}