.password-generator-page{
    padding-top:70px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}

.password-generator-page .shared-banner{
    padding:7% 7.5% 8%;
    .inner-banner{
        width: 100%;
        text-align: center;
    }
    h5{
        margin: 0 auto;
    }
}

@media(max-width:767px){
    .password-generator-page .shared-banner{
        padding:65px 7.5% 100px;
    }
}