.password-generator-page .features .row4{
    margin:5% 0;
    padding:0 10% 0 5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .info{
        width: 40%;
    }
    .images{
        width: 42.5%;
        position: relative;
        &:hover{
            .pass{
                color:#2f56b1;
            }
        }
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
    .img1{
        content: url('../../../../assets/PasswordGenerator/img3.png');
        position: absolute;
        width: 85%;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        border-radius: 25px;
    }
    .bg{
        content: url('../../../../assets/PasswordGenerator/bg.png');
        width: 100%;
    }
    .pass{
        padding: 25px 35px;
        border:1px solid #f8f8f8;
        border-radius: 10px;
        background-color: #fff;
        color:#427cff;
        font-size: 45px;
        position: absolute;
        top:45%;
        right: 0;   
        svg{
            margin:0 5px -5px;
        }
    }
}

@media(max-width:1401px){
    .password-generator-page .features .row4{
        h1{
            font-size: 27.5px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
        }
        .img1{
            border-radius: 20px;
        }
        .pass{
            padding: 20px 30px;
            font-size: 37px;
        }
    }
}

@media(max-width:1000px){
    .password-generator-page .features .row4{
        padding:0 5%;
        .images{
            width: 45%;
        }
        .info{
            width: 42%;
        }
    }
}

@media(max-width:767px){
    .password-generator-page .features .row4{
        padding:0 7.5%;
        margin: 35px 0;
        flex-wrap: wrap;
        .info{
            width: 100%;
            margin:20px auto;
        }
        .images{
            width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 22.5px;
        }
    }
}